import React, { useEffect, useState } from "react"
import createWaflashModule, { initDragAndDrop } from "../../static/wasm/waflash.mjs"



const Waflash = (props) => {
    useEffect(() => {
        const src = props.src && props.src.publicURL ? window.location.origin + props.src.publicURL : '';
        let waflash;
        var m = {
            arguments: [ src ],
            postRun: [],
            locateFile: function (path, prefix) {
                return "/wasm/" + path;
            },
            print: function (text) {
                if (arguments.length > 1) text = Array.prototype.slice.call(arguments).join(' ');
                console.log(text);
            },
            printErr: function (text) {
                if (arguments.length > 1) text = Array.prototype.slice.call(arguments).join(' ');
                console.error(text);
            },
            canvas: (function () {
                const canvas = document.getElementById("canvas");
                // As a default initial behavior, pop up an alert when webgl context is lost. To make your
                // application robust, you may want to override this behavior before shipping!
                // See http://www.khronos.org/registry/webgl/specs/latest/1.0/#5.15.2
                canvas.addEventListener("webglcontextlost", function (e) { alert('WebGL context lost. You will need to reload the page.'); e.preventDefault(); }, false);
                return canvas;
            })(),
        };
        
        // window.onerror = function (message, url, line) {
        //     console.log("Waflash> window.onerror; message=", message, ", url=", url, ", line=", line);
        // };

        createWaflashModule(m).then((o) => {
            console.log("Waflash module created!");
            waflash = o;

            if (!src) initDragAndDrop(waflash);
        });
     
        return () => {
            console.log("Waflash component will unmount!");
            waflash = null;
        };
    })

    const canvasStyle = {
        paddingRight: 0,
        marginLeft: 'auto',
        marginRight: 'auto',
        display: 'block',
        border: '0px none',
        backgroundColor: 'black',
        width: props.width || '100%',
        height: props.height || '480px',
    };

    return (
        <div style={{ position: 'relative', border: `1px solid black` }}>
            <canvas id="canvas" style={canvasStyle} onContextMenu={(e) => e.preventDefault()} tabIndex={-1}></canvas>
        </div>
    )
}

export default Waflash
